export const tabReducer = (state = {
  tabs: {pay: '', income: ''},
  selectedTab: 'pay'
}, action: { type: string }) => {
  switch (action.type) {
    case 'switch_pay':
      return {...state, selectedTab: 'pay'};
    case 'switch_income':
      return {...state, selectedTab: 'income'};
    default:
      return state;
  }
};